<template>
  <div v-show="props.compType === 'date'">
    <el-form-item label="ID">
      <el-tooltip class="item" effect="dark" content=",ID" placement="left">
        <el-input class="input" v-model="props.id" @change="handlerChangeId"></el-input>
      </el-tooltip>
    </el-form-item>
    <el-form-item label="">
      <el-input class="input" v-model="props.label"></el-input>
    </el-form-item>
    <el-form-item label="">
      <el-input-number v-model="props.gutter"  :min="0"></el-input-number>
    </el-form-item>
    <el-form-item label="">
      <el-input-number v-model="props.labelWidth"  :min="1" :max="200"></el-input-number>
    </el-form-item>
    <el-form-item label="">
      <el-switch v-model="props.showLabel"></el-switch>
    </el-form-item>
    <el-form-item  label="">
      <el-input class="input" v-model="props.placeholder"></el-input>
    </el-form-item>
    <el-form-item label="">
      <el-switch v-model="props.required"></el-switch>
    </el-form-item>
    <el-form-item label="">
      <el-select class="input" v-model="props.type" @change="handlerFormatChange">
        <el-option v-for="item in dateTypeOptions" :key="item.value" :label="item.label" :value="item.value"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="">
      <el-input class="input" :value="props.format" @change="handlerChangeValueFormat"/>
    </el-form-item>
    <el-form-item label="">
      <el-switch v-model="props.clearable"></el-switch>
    </el-form-item>
    <el-form-item label="">
      <el-switch v-model="props.readonly"></el-switch>
    </el-form-item>
    <el-form-item label="">
      <el-switch v-model="props.disabled"></el-switch>
    </el-form-item>
    <el-form-item label="" v-show="props.type === 'monthrange'||props.type === 'daterange'||props.type === 'datetimerange'">
      <el-input v-model="props['range-separator']"></el-input>
    </el-form-item>
    <el-form-item label="">
      <el-date-picker class="input" v-model="props.value" value-format="yyyy-MM-dd" type="date" placeholder=""/>
    </el-form-item>
    
  </div>
</template>
<script>
import {changeId} from '../mixin'
const dateType =[
    {
      label: '(date)',
      value: 'date'
    },
    {
      label: '(week)',
      value: 'week'
    },
    {
      label: '(month)',
      value: 'month'
    },
    {
      label: '(year)',
      value: 'year'
    },
    {
      label: '(datetime)',
      value: 'datetime'
    },
    {
      label: '',
      value: 'monthrange'
    },
    {
      label: '',
      value: 'daterange'
    },
    {
      label: '',
      value: 'datetimerange'
    }
  ];
const dateTimeFormat = {
  date: 'yyyy-MM-dd',
  week: 'yyyy  WW ',
  month: 'yyyy-MM',
  year: 'yyyy',
  datetime: 'yyyy-MM-dd HH:mm:ss',
  daterange: 'yyyy-MM-dd',
  monthrange: 'yyyy-MM',
  datetimerange: 'yyyy-MM-dd HH:mm:ss'
}

export default {
  name:"inputConfig",
  props:['props','getFormId'],
  components: {
  },
  mixins:[changeId],
  data(){
    return {
      dateTypeOptions:dateType
    }
  },
  methods:{
    handlerFormatChange(val){
      this.props.format = dateTimeFormat[val];
      this.props['value-format'] = dateTimeFormat[val];
    },
    handlerChangeValueFormat(val){
      this.props['value-format'] = val;
    }
  },
  mounted(){
  }
}
</script>
<style scoped>
.input{
  width:75%
}
</style>
